					</el-form-item>
					<el-form-item label="">
						<el-input v-model="form.image_type"></el-input>
					</el-form-item>
					<el-form-item label="">
						<el-upload action="https://upload-z2.qiniup.com" ref="upload" :limit="1" :before-upload="beforeAvatarUpload"
